<template lang="pug">
  div
    van-form
      van-cell-group(inset title="用户标识")
        van-field(v-model="userIds" :rules="rules.userIds" label="用户ID" placeholder="请输入用户ID(多个用,分割)")
    van-button(type="info" style="width: calc(100% - 32px); margin: 10px 16px 0 16px;" @click="saveConfig") 保存
    van-button(style="width: calc(100% - 32px); margin: 10px 16px 0 16px;" @click="resetConfig") 重置
</template>

<script>
import { getUserinfo } from '@/services/daka'

export default {
  data() {
    return {
      userIds: '',
      rules: {
        userIds: [{
          validator: () => {
            const userIdArr = this.str2Arr(this.userIds)
            const hasNaN = userIdArr.find((userId) => isNaN(userId))
            return !hasNaN
          },
          message: '用户ID格式不正确'
        }]
      },
      saveLoading: false
    }
  },
  created() {
    try {
      const userIds = JSON.parse(localStorage.getItem('config')).users.map(r => r.id)
      this.userIds = userIds.join(',')
    } catch {
      console.warn('未配置用户')
    }
  },
  methods: {
    str2Arr(str) {
      return Array.from(new Set(str
        .replaceAll('，', ',')
        .split(',')
        .map(item => item.trim())
        .filter(item => item !== '')))
    },
    // 保存配置
    async saveConfig() {
      this.saveLoading = true
      const users = []
      const ids = this.str2Arr(this.userIds)
      for (let i = 0; i < ids.length; i++) {
        const userInfo = await getUserinfo(ids[i])
        users.push(userInfo)
      }
      localStorage.setItem('config', JSON.stringify({
        users
      }))
      this.$toast.success('保存成功')
      this.saveLoading = false
    },
    // 重置配置
    resetConfig() {
      localStorage.removeItem('config')
      this.userIds = ''
      this.$toast.success('重置成功')
    }
  }
}
</script>
